import { useState } from "react";
import { useDispatch } from "react-redux"
import { UseAuth } from "../context/AuthContext";
import authService from "../services/authService";
export const useForm = (initialForm) => {
    
    const addressRegex = /^\d+\s[A-z]+\s[A-z]+/g;
    const phoneRegex = /(84|0[3|5|7|8|9])+([0-9]{8})\b/g;
    const emailRegex = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
    const passwordRegex = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{6,32}$/
    const [form, setForm] = useState(initialForm);
    const {
        area, setArea, setSetPM, setPM, error, setError, billingInfo, setBillingInfo, selectedOption, setSelectedOption
    } = UseAuth()
    const [check, setCheck] = useState(false);
    const handelClick = () => {
        setCheck(!check);
    };

    const onChange = (name) => (ev) => {
        setForm({
            ...form,
            [name]: ev.currentTarget.value,
        });
    };

    const register = (name) => {
        return {
            name: name,
            onChange: onChange(name),
            value: form[name],
        };
    };

    const validateLogin = (e) => {
        const errorLogin = {}
        if (!form.username) {
            errorLogin.username = "dien username"

        } else if (!emailRegex.test(form.username)) {
            errorLogin.username = "username khng phi email"
        }
        if (!form.password) {
            errorLogin.password = "password khng uoc  trng"
        }
        setError(errorLogin);
        return errorLogin;
    }
    const validateRegis = () => {
        // e.preventDefault();
        const errorLogin = {}
        if (!emailRegex.test(form.username)) {
            errorLogin.email = "username khng phi email"
        } else if (!form.username) {
            errorLogin.username = "username khng c  trng"
        }
        if (!form.name) {
            errorLogin.name = "in name"
        }
        if (!form.password) {
            errorLogin.password = "password khng uoc  trng"
        } if (form.password !== form.confirm) {
            errorLogin.confirm = "password nhp li khng ng"
        }
        setError(errorLogin);
        return errorLogin;
    }
    const validateCard = () => {
        const errorObject = {};
        if (!form.card) {
            errorObject.card = "Khng c  trng";
        }
        if (!form.cardHolder) {
            errorObject.cardHolder = "Khng c  trng";
        }
        if (!form.expiration) {
            errorObject.expiration = "Khng c  trng";
        }
        if (!form.cvc) {
            errorObject.cvc = "Khng c  trng";
        }
        setError(errorObject);
        return errorObject;
    }

    const validate = () => {
        let errorObject = {};
        if (!form.firstName) {
            errorObject.firstName = "Khng c  trng";
        }
        if (!form.lastName) {
            errorObject.lastName = "Khng c  trng";
        }

        if (!form.emailAddress) {
            errorObject.emailAddress = "Khng c  trng";
        } else if (!emailRegex.test(form.emailAddress)) {
            errorObject.emailAddress = "Vui lng in ng nh dng";
        }
        if (!form.address) {
            errorObject.address = "Khng c  trng";
        }
        if (!form.country) {
            errorObject.country = "Khng c  trng";
        }
        if (!form.phone) {
            errorObject.phone = "Khng c  trng";
        } else if (!phoneRegex.test(form.phone)) {
            errorObject.phone = "Vui lng in ng nh dng";
        }
        if (!form.city) {
            errorObject.city = "Khng c  trng";
        }
        if (!form.code) {
            errorObject.code = "Khng c  trng";
        }

        setError(errorObject);
        return errorObject;
    };

    const submit = (ev) => {
        ev.preventDefault();
        const validateResule = validate();
        console.log(validateResule)
        if (Object.keys(validateResule).length === 0) {
            alert("thanh cong");
            setBillingInfo(true);
            console.log(billingInfo);
        }
    }
    const submitForm = (ev) => {
        ev.preventDefault();

        const alo = validateCard();
        if (Object.keys(alo).length === 0) {
            alert("thanh cong");
            return setSetPM(true);
        }



    }


    // const valiTextArea = () => {
    //     let errorArea = {};
    //     console.log('form.area :>> ', form.area);
    //     if (!area.area) {
    //         errorArea.area = "Khng c  trng";
    //     }
    //     setError(errorArea);

    //     return errorArea
    // }
    // function handleArea(e) {
    //     e.preventDefault()
    //     const areaLog = valiTextArea();
    //     console.log('areaLog :>> ', areaLog);
    //     if (Object.keys(area).length === 0) {
    //         setArea(true);
    //         // console.log(area)
    //     }
    // }

    return {
        error,
        setError,
        check,
        handelClick,
        submit,
        register,
        validate,
        validateLogin,
        form, setForm,
        validateCard, submitForm,
        setBillingInfo, onChange,
        billingInfo, setPM, validateRegis, setSetPM, setPM
        // valiTextArea, handleArea

    }
}


